#szjg .row .col{
    width:750px;
    margin-top:20px;
}
#szjg .row .cardtitle{
    font-size:25px;
    text-align:center;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
}
#szjg .row .coltips{
    min-width:280px;
    max-width:300px;
    margin-top:20px;
}
#szjg .row .coltips2{
    min-width:150px;
    max-width:300px;
    margin-top:20px;
}
#szjg .shaixuan_box{
    position:relative;
    left:13%;
    bottom:32px;
}
@media all and (max-width: 1200px) {
    #szjg .shaixuan_box{
        left: 3%!important;
    }
}
#szjg .row .tipheight{
    height:435px;
}
#szjg .row .tip_select{
    height:465px;
}
#szjg .row .tips{
    font-size:25px;
    text-align:left;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
    padding:0 20px;
}
#szjg .row .tippoint{
    margin-top:50px;
    padding:0 20px;
    line-height:40px;
}
#szjg .is-active{
    color: #029348 !important;
}
.el-tabs__active-bar{
    background: #029348 !important;
}
#szjg .is-top :hover{
    color: #029348 !important;
}
#szjg .is-left :hover{
    color: #029348 !important;
}
#szjg .el-card{
    padding: 40px 0 40px 0;
}
#szjg .shaixuan{
    padding: 82px 0 32px 0;
}
.el-tabs__content{
    overflow:inherit;
    padding-bottom: 20px;
}
.el-card{
    transition: 0s;
}
</style>



